"use client"

import { useState, useEffect } from "react"
import type { ColumnDef } from "@tanstack/react-table"
import { Edit, Trash2, Eye, Tag, Plus } from "lucide-react"
import { useStore } from "@/store"
import { DataTable } from "@/components/ui/data-table"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import type { Category } from "@/types/category"
import axios from "@/utils/axiosConfig";
import { useToast } from "@/hooks/use-toast"

export default function CategoriesPage() {
  const { toast } = useToast()

  const [categories, setCategories] = useState<Category[]>([])

  const [loading, setLoading] = useState(true)

  const loadCategories = async () => {
    try {
      setLoading(true)
      const response = await (await axios.get("/category/showCategory"))
      const data: Category[] = response.data.result || []
      setCategories(data)
    } catch (error) {
      toast({
        title: "Error",
        description: "Failed to load categories",
        variant: "destructive",
      })
    } finally {
      setLoading(false)
    }
  }

  useEffect(() => {
    loadCategories()
  }, [toast])

  const handleView = (category: Category) => {
  }

  const handleEdit = (category: Category) => {
  }

  const handleDelete = (category: Category) => {
  }

  const handleCreate = () => {
  }

  const columns: ColumnDef<Category>[] = [
    {
      accessorKey: "id",
      header: "ID",
    },
    {
      accessorKey: "imageName",
      header: "Image",
      cell: ({ row }) => {
        const imageName = row.getValue("imageName") as string
        return imageName ? (
          <div className="flex items-center">
            <div className="h-10 w-10 overflow-hidden rounded-md border">
              <img
                src={imageName || '/images/placeholder.svg'}
                alt={imageName}
                className="h-full w-full object-cover"
              />
            </div>
          </div>
        ) : (
          <span className="text-muted-foreground">No image</span>
        )
      },
    },
    {
      accessorKey: "name",
      header: "Name",
    },
    {
      accessorKey: "isActive",
      header: "Status",
      cell: ({ row }) => {
        const isActive = row.getValue("isActive") as boolean
        return <Badge variant={isActive ? "default" : "secondary"}>{isActive ? "Active" : "Inactive"}</Badge>
      },
    },
    {
      id: "actions",
      header: "Actions",
      cell: ({ row }) => {
        const category = row.original

        return (
          <div className="flex gap-2">
            <Button variant="outline" size="sm" onClick={() => handleView(category)}>
              <Eye className="h-4 w-4" />
            </Button>
            <Button variant="outline" size="sm" onClick={() => handleEdit(category)}>
              <Edit className="h-4 w-4" />
            </Button>
            <Button
              variant="outline"
              size="sm"
              onClick={() => handleDelete(category)}
              className="text-red-500 hover:text-red-700"
            >
              <Trash2 className="h-4 w-4" />
            </Button>
          </div>
        )
      },
    },
  ]

  return (
    <div className="space-y-4">
      <div className="flex items-center justify-between">
        <h1 className="text-2xl font-bold">Categories Management</h1>
        <Button onClick={handleCreate}>
          <Plus className="mr-2 h-4 w-4" />
          Add Category
        </Button>
      </div>

      <div className="grid gap-4 md:grid-cols-3">
        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium">Total Categories</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="flex items-center">
              <Tag className="mr-2 h-5 w-5 text-blue-600" />
              <div className="text-2xl font-bold">{categories.length}</div>
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium">Active Categories</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="flex items-center">
              <Tag className="mr-2 h-5 w-5 text-green-600" />
              <div className="text-2xl font-bold">{categories.filter((category) => category.isActive).length}</div>
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium">Inactive Categories</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="flex items-center">
              <Tag className="mr-2 h-5 w-5 text-gray-600" />
              <div className="text-2xl font-bold">{categories.filter((category) => !category.isActive).length}</div>
            </div>
          </CardContent>
        </Card>
      </div>

      <Card>
        <CardHeader>
          <CardTitle>All Categories</CardTitle>
        </CardHeader>
        <CardContent>
          {loading ? (
            <div className="flex h-40 items-center justify-center">
              <p>Loading categories...</p>
            </div>
          ) : (
            <DataTable columns={columns} data={categories} searchColumn="name" searchPlaceholder="Search by name..." />
          )}
        </CardContent>
      </Card>

      {/* Modals */}
      {/* <CreateCategoryModal />
      <EditCategoryModal />
      <ViewCategoryModal />
      <DeleteCategoryModal /> */}
    </div>
  )
}

